
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Jump3',
components: {
XButton
},
data() {
return {
msg1: '',
msg2: []
}
},
methods: {
},
mounted: function () {
this.$nextTick(function () {
this.msg1 = '全路径：' + this.$route.fullPath
let params = this.$route.fullPath.split('?')[1].split('&')
let a = ''
for (let i = 0, len = params.length; i < len; i++) {
let arr = params[i].split('=')
let a = {key: arr[0], value: decodeURI(arr[1])}
this.msg2.push(a)
}
})
},
}



