
import XButton from 'vux/src/components/x-button/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


import Tab1 from '../components/Tab1.vue'
import Tab2 from '../components/Tab2.vue'
import Tab3 from '../components/Tab3.vue'
import Tab4 from '../components/Tab4.vue'

export default {
name: 'Tab',
components: {
XButton, Tabbar, TabbarItem,Tab1,Tab2,Tab3,Tab4
},
data() {
return {
selectItem: 1,
notifyChildMsg:'这是父组件向子组件传递的数据',
}
},
methods: {

change: function (index) {
this.selectItem = index+1
console.log('-----------'+this.selectItem)
},
notify:function () {
this.notifyChildMsg = '子组件已经通知了父组件'
},
},
}



