
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

import StorageUtil from '../util/StorageUtil'

export default {
name: 'Storage',
components: {
XButton,
XInput,
Group
},
data() {
return {
value: '',
msg: ''
}
},
methods: {
getStorage: function () {
let a = StorageUtil.getStorage('key')
this.msg = (a == null || a == '') ? '空' : a
},
setStorage: function () {
StorageUtil.setStorage('key', this.value)
},
deleteStorage: function () {
StorageUtil.deleteStorage('key')
this.msg = ''
},
}
}



