

export default {

  /**
   * 获取
   */
  getStorage(key){
    return JSON.parse(window.localStorage.getItem(key))
  },

  /**
   * 存储
   */
  setStorage(key,value){
    window.localStorage.setItem(key,JSON.stringify(value))
  },

  /**
   * 删除某一条数据
   */
  deleteStorage(key){
    window.localStorage.removeItem(key)
  },

  /**
   * 删除所有数据
   */
  cleanAll(){
    window.localStorage.clear()
  }
}



