
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

const list = () => ['精选', '美食', '电影', '酒店', '外卖']

export default {
name: 'Tabb',
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
XButton
},
data() {
return {
msg: '已发货',
selectItem: 0,
demo: '',
list: list()
}
},
methods: {
onItemClick(index) {
if (index == 0) {
this.msg = '已发货'
} else if (index == 1) {
this.msg = '未发货'
} else {
this.msg = '全部订单'
}
},
addTab() {
if (this.list.length < 5) {
this.list = list().slice(0, this.list.length + 1)
}
},
removeTab() {
if (this.list.length > 1) {
this.list = list().slice(0, this.list.length - 1)
}
},
next() {
if (this.selectItem === this.list.length - 1) {
this.selectItem = 0
} else {
++this.selectItem
}
},
prev() {
if (this.selectItem === 0) {
this.selectItem = this.list.length - 1
} else {
--this.selectItem
}
},


},
}



