
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


export default {
name: 'Refresh',
components: {
XButton,
Scroller,
Spinner,
Divider,
},
data() {
return {
page: 2,
refreshData: ['数据1', '数据2', '数据3', '数据4', '数据5', '数据6', '数据7', '数据8',],
loadData: ['数据1', '数据2', '数据3', '数据4', '数据5', '数据6', '数据7', '数据8', '数据9', '数据10'],
refreshStatus: {pulldownStatus: 'default'},
pulldownConfig: {content: '下拉刷新', downContent: '下拉刷新', upContent: '释放刷新', loadingContent: '加载中...'},
pullupConfig: {content: '上拉加载更多', downContent: '松开进行加载', upContent: '上拉加载更多', loadingContent: '加载中...'}
}
},
methods: {
refresh() {
let that = this
setTimeout(function () {
that.refreshStatus.pulldownStatus = 'default'
}, 2000)
},

load() {
let that = this
let data = []
for (let i = 1; i <= 10; i++) {
data.push('数据  ' + ((this.page - 1) * 10 + i))
}
setTimeout(function () {
for (let i = 0; i < 10; i++)
that.loadData.push(data[i])
that.$refs.scroller.donePullup()
that.page++
}, 2000)
},

jump(){
this.$router.push('/Refresh2')
}
},

}



