
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'


export default {
name: 'Base',
components: {
XButton,
Group,
XInput,
XImg,
XSwitch,
},
data() {
return {
msg: '',
isShow: true,
showStyle: true,
style2: 'textStyle2',
style3: 'textStyle3',
screenWH: '',
screenWith: '',
}
},
methods: {
show: function () {
this.$vux.toast.show({
text: this.msg,
type: 'text',
position: 'bottom'
})
},
img: function (msg) {
this.$vux.toast.show({
text: msg,
type: 'text',
position: 'bottom'
})
},
show1: function () {
this.$vux.alert.show({
title: '版本',
content: '该版本是最新版本',
onShow() {
console.log('Plugin: I\'m showing')
},
onHide() {
console.log('Plugin: I\'m hiding')
}
})
},
show2: function () {
this.$vux.confirm.show({
title: '删除',
content: '你确定要删除该条信息？',
onCancel() {
console.log('---------onCancel')
},
onConfirm() {
console.log('---------onConfirm')
}
})
},
show3: function () {
this.img('Datetime测试时有bug')
},
show4: function () {
let that = this
this.$vux.loading.show({
text: 'Loading'
})
setTimeout(function () {
that.$vux.loading.hide()
}, 2000)
},
getScreenWH() {
this.screenWH = '本屏幕的高：' + window.screen.height + '      宽：' + window.screen.width
this.screenWith = window.screen.width + 'px'
},
},
computed: {

reversedMessage: function () {
return this.msg.split('').reverse().join('')
}
},
watch: {

msg(val, oldVal) {
console.log('--------------', val + ' ' + oldVal)
}
},
}



