
import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
name: 'Scroll',
components: {
XButton,
Scroller,
},
data() {
return {
horizontalListData: [],
horizontalScrollWitdh:'100px',
scrollTop:'',
}
},
methods: {
getDataH: function () {
let data = [1,2,3,4,5,6,7]
let length = data.length
this.horizontalListData = data
this.horizontalScrollWitdh = length*200+(length-1)*10+'px'
},
toHeader:function () {
this.$nextTick(() => {
this.$refs.scrollerEvent.reset({top: 0})
})
},

onScroll:function (pos) {
this.scrollTop = pos.top
},
toast:function (msg) {
this.$vux.toast.show({
text: msg+'',
type: 'text',
position: 'bottom'
})
}
}
}



