  export default {
    name: 'app',
    data() {
      return {
        transitionName: 'slide-left',
        activitylist: ['/'],// 用栈来管理页面
      }
    },
    watch: {
      '$route'(to, from) {
        if (this.activitylist.length == 1) {
          if (to.path == this.activitylist[0]) {
            this.transitionName = 'slide-right';
          } else {
            this.transitionName = 'slide-left';
          }
          this.activitylist.push(to.path)
        } else {
          if (to.path == this.activitylist[this.activitylist.length - 2]) {
            this.transitionName = 'slide-right'
            this.activitylist.pop()
          } else {
            this.transitionName = 'slide-left';
            this.activitylist.push(to.path)
          }
        }
      }
    }
  }
