import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home.vue'
import Base from '../components/Base.vue'
import Life from '../components/Life.vue'
import Jump from '../components/Jump.vue'
import Jump2 from '../components/Jump2.vue'
import Storage from '../components/Storage.vue'
import List from '../components/List.vue'
import Http from '../components/Http.vue'
import Tab from '../components/Tab.vue'
import Scroll from '../components/Scroll.vue'
import Refresh from '../components/Refresh.vue'
import Refresh2 from '../components/Refresh2.vue'
import Animation from '../components/Animation.vue'
import Tabb from '../components/Tabb.vue'
import Jump3 from '../components/Jump3.vue'
import JsToJava from '../components/JsToJava.vue'



import ToastPlugin from 'vux/src/plugins/toast/index.js'

import AlertPlugin from 'vux/src/plugins/alert/index.js'

import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import VueAnimate from 'vue2-animate/dist/vue2-animate.min.css'

Vue.use(VueAnimate)
Vue.use(Router)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(DatetimePlugin)
Vue.use(LoadingPlugin)

export default new Router({
routes: [
{path: '/', name: 'Home', component: Home},
{path: '/Base', name: 'Base', component: Base},
{path: '/Life', name: 'Life', component: Life},
{path: '/Jump', name: 'Jump', component: Jump},
{path: '/Jump2', name: 'Jump2', component: Jump2},
{path: '/Storage', name: 'Storage', component: Storage},
{path: '/List', name: 'List', component: List},
{path: '/Http', name: 'Http', component: Http},
{path: '/Tab', name: 'Tab', component: Tab},
{path: '/Scroll', name: 'Scroll', component: Scroll},
{path: '/Refresh', name: 'Refresh', component: Refresh},
{path: '/Refresh2', name: 'Refresh2', component: Refresh2},
{path: '/Animation', name: 'Animation', component: Animation},
{path: '/Tabb', name: 'Tabb', component: Tabb},
{path: '/Jump3', name: 'Jump3', component: Jump3},
{path: '/JsToJava', name: 'JsToJava', component: JsToJava},
]
})



