
import XButton from 'vux/src/components/x-button/index.vue'

import Constant from '../util/Constant'

export default {
name: 'Jump',
components: {
XButton
},
data() {
return {
msg: ''
}
},
methods: {
jumpWithParams: function () {

this.$router.push({path: '/Jump2', name: 'Jump2', params: {name: '张三', id: 3}})
},
refresh: function () {
this.$router.go(0)
},
back: function () {
this.$router.go(-1)
},
},


created(){
this.msg = Constant.message
Constant.message = ''
}


}



