
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'

export default {
name: 'Http',
components: {
XButton
},
data() {
return {
msg: '',
source: '',
}
},
methods: {
getHttp: function () {
let that = this
axios.get('http://10.10.16.28:8081/Test/testget', {params: {id: 1}})
.then(res => {
console.log('--------------get', res.data.datum)
that.msg = res.data.datum
})
.catch(error => {
console.log('--------------get', error)
that.msg = 'get请求失败'
})
},
postHttp: function () {

let that = this
let param = new URLSearchParams();
param.append("id", "2");
axios.post('http://10.10.16.28:8081/Test/testpost', param, {
headers: {
'Content-Type': 'application/x-www-form-urlencoded'
}
}
)
.then(res => {
console.log('--------------post', res.data.datum)
that.msg = res.data.datum
})
.catch(error => {
console.log('--------------post', error)
that.msg = 'post请求失败'
})
},
getCancel: function () {
this.source = axios.CancelToken.source()
let that = this
axios.get('http://10.10.16.28:8081/Test/testgetcancel', {
cancelToken: that.source.token,
params: {id: 3}
}
)
.then(res => {
console.log('--------------cancel', res.data.datum)
that.msg = res.data.datum
})
.catch(error => {
if ('Cancel' == error.toString())
that.msg = '取消请求'
else
that.msg = 'get请求失败'
console.log('--------------cancel', error)
})
},
cancel: function () {
this.source.cancel()
},
loadingHttp:function () {
this.source = axios.CancelToken.source()
let that = this
this.beforeHttp()
axios.get('http://10.10.16.28:8081/Test/testgetcancel', {
cancelToken: that.source.token,
params: {id: 3}
}
)
.then(res => {
console.log('--------------cancel', res.data.datum)
that.msg = res.data.datum
that.afterHttp()
})
.catch(error => {
if ('Cancel' == error.toString())
that.msg = '取消请求'
else
that.msg = 'get请求失败'
console.log('--------------cancel', error)
that.afterHttp()
})
},
beforeHttp(){
this.$vux.loading.show({
text: '加载中...'
})
},
afterHttp(){
this.$vux.loading.hide()
},

}
}



