
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'


export default {
name: 'JsToJava',
components: {
XButton
},
data() {
return {}
},
methods: {
call: function (number) {
window.obj.jsCallJava(number);
},
javaCallJs: function (msg) {
this.$vux.toast.show({
text: msg + '：这是vue Toast',
type: 'text',
position: 'bottom'
})
},
},

created() {
window.javaCallJs = this.javaCallJs
},

}




