
import XButton from 'vux/src/components/x-button/index.vue'

import Constant from '../util/Constant'

export default {
name: 'Jump2',
components: {
XButton
},
data() {
return {
msg: 'aaa'
}
},
methods: {
show: function () {
let params = this.$route.params
this.msg = 'name:' + params.name + '  id:' + params.id
console.log('------',this.$route.fullPath.split('?')[1].split('&'))
let aaa = this.$route.fullPath.split('?')[1].split('&')[0].split('=')[1]
console.log('------',decodeURI(aaa))
},
back:function () {
Constant.message = '这是返回时携带的参数'
this.$router.go(-1);
}
},
}



