
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'List',
components: {
XButton,
},
data() {
return {
data: [
{name: "张三", id: 5},
{name: "李四", id: 4},
{name: "网麦子", id: 2},
{name: "考虑福", id: 8},
{name: "王麻子", id: 9},
]
}
},
methods: {
show: function (index) {
this.$vux.toast.show({
text: index + '  ' + this.data[index].name,
type: 'text',
position: 'bottom'
})
},
addData: function () {
let a = {name: "增加的数据", id: 10}
this.data.push(a)
},
deleteData: function () {
this.data.pop()
},
replaceData: function () {
let newData = [{name: "数据1", id: 1}, {name: "数据2", id: 2}, {name: "数据3", id: 3}]
this.data = newData
},
clean: function () {
this.data = []
}
}
}



