
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


export default {
name: 'Refresh2',
components: {
Scroller,
Spinner
},
data() {
return {
page: 1,
listData: [],
refreshStatus: {pullupStatus: 'default', pulldownStatus: 'default'},
pulldownConfig: {content: '下拉刷新', downContent: '下拉刷新', upContent: '释放刷新', loadingContent: '加载中...'},
pullupConfig: {content: '上拉加载更多', downContent: '松开进行加载', upContent: '上拉加载更多', loadingContent: '加载中...'}
}
},
methods: {
refresh() {
console.log('-----------------refresh')
this.page = 1
this.getData()
},

load() {
console.log('-----------------load')
this.getData()
},

getData () {
let that = this
let data = []
for (let i = 1; i <= 10; i++) {
data.push('数据  ' + ((this.page - 1) * 10 + i))
}

setTimeout(function () {
if (that.page == 1) {
that.listData = data
that.$refs.scroller.donePulldown()
that.$refs.scroller.enablePullup()
} else {
for (let i = 0; i < 10; i++)
that.listData.push(data[i])
that.$refs.scroller.donePullup()
if (that.page == 4) {
that.$refs.scroller.disablePullup()
}
}
that.page++
}, 2000)
}

},

mounted: function () {
let that = this
this.$nextTick(function () {
that.$refs.scroller.disablePullup()
that.refresh()
})
}
}



